import changeState from '../helpers/change-state';
import { displayState } from './shared';

const summaryApp = (state = {}, action) => {
  switch (action.type) {
    case 'UPDATE_TRENDS_REQUEST':
      return changeState(state, { trendsRequest: action.trendsRequest });
    case 'UPDATE_DATE_RANGE_STRING':
      return changeState(state, { dateRange: action.dateRange });
    case 'UPDATE_TRENDS_DATA':
      return changeState(state, { trendsData: action.trendsData });
    case 'SHOW_LOADING':
    case 'HIDE_LOADING':
    case 'SHOW_ERROR':
      return displayState(state, action);
    default:
      return state;
  }
};

export default summaryApp;



